import logo from './logo.svg';
import './App.css';
import {useState} from "react";
function App() {
  let[oldexpression,setoldExpression]=useState("-");

  let[expression,setExpression]=useState("0");
  
  let[prev,setPrev]=useState("ANS");
  
  let numerics = new Set("0123456789.()");
  
  let operators = new Set("+-/*%");
  
  let buttons =["(",")","%","AC","7","8","9","/","4","5","6","*","1","2","3","-","0",".","=","+"];
  
  let evaluation =function(){
    try{
      let evaluate =eval(expression);
      setoldExpression("ANS = "+evaluate);
      setExpression(String(evaluate));
      setPrev("ANS");  
    }
    catch{
      setExpression("ERROR");
    }
  };

  let putNumerics = function(value){
    if(prev=="ANS")
    {
      setoldExpression("ANS = "+ expression);
      setExpression(value);
    }
    else
    {
      setExpression(expression+value);
    }
    setPrev("NUM");
  };

  let putOperator = function(value){
    if(prev!="OP")
    {
      setExpression(expression+value);
    }
    else
    {
      setExpression(expression.slice(0,-1)+value);
    }
    setPrev("OP");
  };

  let putDelete = function () {
    if (expression.length >= 1) {
      if(expression==="ERROR"){
        setExpression(expression.slice(0,-5));
      }
      else{
      setExpression(expression.slice(0, -1));
      }
    }
    setPrev("DEL");
  };

  let handleKeyUp = function(event){
    console.log(event.key);
    if (event.key==="Backspace")
    {
      putDelete();
    }
    else if(numerics.has(event.key))
    {
      putNumerics(event.key);
    }
    else if(operators.has(event.key))
    {
      putOperator(event.key);
    }
    else if(event.key==="Enter")
    {
      evaluation();
    }
    else if(event.key==="Escape")
    {             
      setExpression("0");
      setPrev("ANS")
    }
  }

  return (
    <div className="App" tabIndex={0} onKeyUp={handleKeyUp}>
      <div style={{
        background:"#444444",
        padding:"10px",
        borderRadius:"10px"
      }}>


        <h1 style={{
          textAlign:"center",
          color:"#ffffff"}}
          >            
           My  Calculator </h1>

      <div
          style={{
            width: "400px",
            background: "#ffffff",
            display: "flex",
            flexDirection: "column",
            alignItems: "flex-end",
            justifyContent: "center",
            padding: "20px",
            borderRadius: "10px",
            overflow: "hidden",
            margin: "20px",
          }}>

      <h5>{oldexpression}</h5>
      <h1>{expression}</h1>

    </div>
    <div style={{
      width:"400px",
      background:"#999999",
      display:"flex",
      flexDirection:"row",
      alignItems:"flex-end",
      justifyContent:"center",
      borderRadius:"10px",
      flexWrap:"wrap",
      margin:"20px",
      padding:"20px"

    }}>
      {buttons.map(function(buttonValue,idx){
        return <button style={{
          width:"90px",
          margin:"5px",
          padding:"10px"
        }}
        onClick={function(){
          if (buttonValue === "AC") {
              putDelete();
          } else if (numerics.has(buttonValue)) {
            putNumerics(buttonValue);
          } else if (operators.has(buttonValue)) {
            putOperator(buttonValue);
          } else if (buttonValue === "=") {
            evaluation();
          }
        }}
        
        >{buttonValue}</button>;
      })}
    </div>
    </div>
    </div>
  );
}

export default App;
